<template>
    <div class="myswiper">
        <swiper :options="swiperOption">
            <swiper-slide v-for="(contentItem, index) in swiperContents" :key="index">
                <div class="swiper-content" :style="{height:height}">
                    <img :src="contentItem.imgPath"/>
                </div>
            </swiper-slide>
            <template v-slot:pagination>
                <div class="swiper-pagination"></div>
            </template>
        </swiper>
    </div>
</template>

<script>
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  components: {
    swiper,
    swiperSlide
  },
  props:{
      height:{
          type: String,
          defalut: 'auto'
      },
      swiperContents:{
          type: Array,
          required: true,
          default: function(){
              return [];
          }
      }
  },
  data: function(){
      return {
            // swiper 配置
            swiperOption: {
                // 自动切换
                autoplay: true,
                // 自动高度，让 siwper 的高度跟随 slide 的高度变化
                autoHeight: true,
                // 设置分页器效果
                pagination: {
                    // 分页器对应的 html 元素
                    el: '.swiper-pagination',
                    // 分页器的样式
                    type: 'bullets',
                    // 分页器内元素的类名
                    bulletClass: 'custom-bullet-class'
                }
            }
      }
  }
}

</script>

<style lang="scss">
    .myswiper{
        box-shadow: 0 0 10px #e5e5e5;
        .swiper-content{
            text-align: center;
        }

        .swiper-pagination{
            bottom: 12px;
            .custom-bullet-class{
                width: 8px;
                height: 8px;
                border: 1px solid #e5e5e5;
                border-radius: 100%;
                box-sizing: border-box;
                display: inline-block;
                margin: 0 4px;
                opacity: 1;
            }
            .swiper-pagination-bullet-active{
                background-color: black;
            }
        }
    }
    
</style>