<template>
    <div class="main">
        <component :is="currentComponet"></component>
        <tool-bar @onChangeToolBar='onChangeToolBar'></tool-bar>   
    </div>
</template>

<script>
import ToolBar from '../components/currency/ToolBar.vue';
import Home from '../components/currency/Home';
import Shopping from '../components/currency/Shopping';
import My from '../components/currency/My';

export default {
    name: 'oliver',
    components:{
        ToolBar,
        home: Home,
        shopping: Shopping,
        my: My
    },

    data:function (){
        return {
            currentComponet:'home'
        }
    },
    
    methods:{
        onChangeToolBar:function(item){
            this.currentComponet = item.id;
            //alert(item.id);
        }
    }
}
</script>

<style lang='scss' scoped>
.main{
    position: absolute;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
}
</style>