<template>
    <div class="tool-bar">
        <div class="tool-bar-item" v-for="(item,index) in toolBarData" :key="index">
            <p class="tool-bar-name" :class="{'tool-bar-name-active' : index === currentIndex}" @click="onChangeToolBar(index,item)">{{item.name}}</p>
        </div>
    </div>
</template>

<script>
export default {
    data:function(){
        return {
            toolBarData:[
                {
                    id: 'home',
                    name: '首页'
                },
                {
                    id: 'shopping',
                    name: '购物车'
                },
                {
                    id: 'my',
                    name: '我的'
                }
            ],
            currentIndex: 0
        }
    },
    methods:{
        onChangeToolBar:function(index,item){
            this.currentIndex = index;
            this.$emit('onChangeToolBar',item);
        }
    }
}
</script>

<style lang='scss' scoped>
@import '../../assets/css/style.scss';

.tool-bar{
    width: 100%;
    height: 46px;
    display: flex;
    justify-content: space-around;
    background-color: white;
    box-shadow: 0 0 16px 0 rgba(0, 0, 0, 0.2);
    .tool-bar-item{
        text-align: center;
        line-height: 46px;
        .tool-bar-name{
            color: black;
            font-size: 16px;
            &-active{
                color: red
            }
        }
    }
}
</style>