import Vue from 'vue'
import axios from 'axios'

// axios.defaults.baseURL = 'http://api.imooc.hybrid.lgdsunday.club/';
axios.defaults.baseURL = 'http://192.168.56.1:8081/';

//请求拦截器
axios.interceptors.request.use(config => {
    if(config.params){
        config.params.token = 'e9b933b0-e4d0-11e9-acd3-7b90f1eac841'
    }else{
        config.params = {
            token : 'e9b933b0-e4d0-11e9-acd3-7b90f1eac841'
        }
    }
    return config;
});

//响应拦截器
axios.interceptors.response.use((response)=>{
    return response.data;
},(error) => {
    return Promise.reject(error);
});

//将axios挂载到Vue中
Vue.prototype.$http = axios;