<template>
    <div class="goods" :style="{height: goodsViewHeight}">
        <div class="goods-item" v-for="(item,index) in goodItems" :key="index" :style="goodItemsStyle[index]">
            <div class="goods-item-img">

            </div>
            <div class="goods-item-desc">
                {{item.desc}}
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data: function(){
        return {
            goodsViewHeight: '100%',
            goodItems: [],
            goodItemsStyle: [],
            MIN_HEIGHT: 150,
            HEIGHT_REGION: 50,
            ITEM_MARGIN_TOP: 12
        }
    },
    created: function(){
        this.initGoodData();
    },
    methods:{
        /**
         * 初始化数据
         */
        initGoodData: function(){
            for(let i = 0; i < 20; i++){
                let item = {};
                item.id = i;
                item.desc = "第" + i + "内容";
                this.goodItems.push(item);
            }
            this.$nextTick(() => {
                this.initWaterFall();
            })
        },
        /**
         * 获取随机高度
         */
        getRandomHeight: function(){
            return Math.floor(Math.random() * this.HEIGHT_REGION + this.MIN_HEIGHT);
        },
        /**
         * 瀑布流布局
         */
        initWaterFall: function(){
            //左边高度
            let leftHeightTotal = 0;
            //右边高度
            let rightHeightTotal = 0;
            this.goodItems.forEach(item => {
                let randomHeight = this.getRandomHeight();
                //如果左边高度 <= 右边高度
                if(leftHeightTotal <= rightHeightTotal){
                    this.goodItemsStyle.push(
                        {
                            left: 0,
                            top: leftHeightTotal + 'px',
                            height: randomHeight + 'px'
                        }
                    );
                    leftHeightTotal += randomHeight;
                }else{
                    this.goodItemsStyle.push(
                        {
                            right: 0,
                            top: rightHeightTotal + 'px',
                            height: randomHeight + 'px'
                        }
                    );
                    rightHeightTotal += randomHeight;
                }
            });
        }
    }
}
</script>

<style lang="scss" scoped>
.goods{
    background-color: #fffefe;
    width: 100%;
    position: relative;
    margin-top: 12px;

    &-item{
        width: 49%;
        position: absolute;
        border-radius: 5px;
        box-shadow: 0 0 8px #e5e5e5;
        display: flex;
        flex-direction: column;


        &-img{
            height: 60%;
            width: 100%;
        }

        &-desc{
            width: 100%;
            height: 38%;
            text-align: center;
            font-size: 12px;
        }
    }
}
</style>